}

.root, .root * { -webkit-box-sizing: border-box; box-sizing: border-box; }

.root {
	display: block;
	font: 16px;
	line-height: 24px;
}

.inputWrapper {
	margin: 8px 0;
  position: relative;
}

.track {
	background-color: #5c6c7c;
	height: 1px;
	position: relative;
	-webkit-transition: background 0.25s ease;
	transition: background 0.25s ease;
	width: 100%;
}

.fill {
	display: none;
}

.thumb {
	background-color: #ffffff;
	border: 1px solid #5c6c7c;
	border-radius: 50%;
	height: 16px;
	left: 50%;
	margin-left: -8px;
	position: absolute;
	top: -8px;
	-webkit-transition: border 0.25s ease, background 0.25s ease;
	transition: border 0.25s ease, background 0.25s ease;
	width: 16px;
}

.input {
	height: 15px;
	outline: none;
	top: -7px;
}

.input::-ms-thumb {
	height: 48px;
	width: 48px;
}

.input::-moz-range-thumb {
	height: 48px;
	width: 48px;
}

.input::-webkit-slider-thumb {
	height: 48px;
	width: 48px;
}

/* focused styles */

.input:focus ~ .track {
	background-color: #006be6;
}

.input:focus ~ .track .thumb {
	border: 2px solid #006be6;
	-webkit-box-shadow: 0 2px 2px 0 rgba(0, 107, 230, 0.2);
	        box-shadow: 0 2px 2px 0 rgba(0, 107, 230, 0.2);
}

.input:focus ~ .outputTooltip,
.root:hover .outputTooltip {
	opacity: 1;
	pointer-events: auto;
	z-index: 100;
}

.output {
	background-color: #5c6c7c;
	color: #ffffff;
	display: inline-block;
	margin: 8px 0 0;
	padding: 8px;
}

.outputTooltip {
	margin: 0;
	opacity: 0;
	pointer-events: none;
	position: absolute;
	top: 20px;
	-webkit-transform: translate(-50%, 0);
	        transform: translate(-50%, 0);
	-webkit-transition: opacity 0.25s ease;
	transition: opacity 0.25s ease;
}

.outputTooltip::before {
	border-left: 8px solid transparent;
	border-right: 8px solid transparent;
	border-bottom: 8px solid #5c6c7c;
	content: "";
	left: 50%;
	margin-left: -8px;
	margin-top: -8px;
	position: absolute;
	top: 0;
}

.vertical .outputTooltip {
	left: 20px;
	top: auto;
	-webkit-transform: translate(0, -50%);
	        transform: translate(0, -50%);
}

.vertical .outputTooltip::before {
	border-bottom: 8px solid transparent;
	border-left: 0;
	border-right: 8px solid #5c6c7c;
	border-top: 8px solid transparent;
	left: 0;
	top: 50%;
}

/* vertical slider */

.vertical .input,
.vertical .track {
	-webkit-transform-origin: 0 0;
	        transform-origin: 0 0;
	-webkit-transform: rotate(-90deg) translate(-100%, 0);
	        transform: rotate(-90deg) translate(-100%, 0);
}

/* disabled and readonly styles */

.disabled .track,
.readonly .track {
	background-color: #d6dde2;
}

.disabled .thumb,
.readonly .thumb {
	background-color: #f4f6f7;
}

/* invalid style */

.invalid .track {
	background-color: #eb1313;
}

.invalid .thumb {
	border-color: #eb1313;
}

/* valid style */

.valid .track {
	background-color: #188701;
}

.valid .thumb {
	border-color: #188701;
}



