.root,
.root *,
.root *::before,
.root *::after {
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
}

.root {
	border: 1px solid #d6dde2;
	display: inline-block;
	font-size: 16px;
	line-height: 24px;
	margin: 0 18px;
	position: relative;
	width: 284px;
}

.dateGrid {
	width: 100%;
}

/* Date grid */

.weekday, .date {
	text-align: center;
	width: 14.28%;
}

.weekday {
	background-color: #f4f6f7;
	border-bottom: 1px solid #d6dde2;
	color: #5c6c7c;
	font-weight: normal;
	padding: 0 4px;
	text-align: center;
	font-size: 14px;
	text-transform: uppercase;
}

.weekday abbr {
	text-decoration: none;
}

.date {
	color: #5c6c7c;
	cursor: pointer;
	font-weight: bold;
	outline: 1px solid transparent;
	padding: 8px;
	-webkit-transition: all 0.25s ease;
	transition: all 0.25s ease;
}

.todayDate {
	background-color: #f4f6f7;
}

.date:focus,
.date:hover {
	color: #006be6;
	outline: 1px solid #d6dde2;
}

.inactiveDate {
	color: #d6dde2;
}

.selectedDate {
	background-color: #006be6;
	color: #ffffff;
}

.selectedDate:focus,
.selectedDate:hover {
	background-color: #ffffff;
	color: #006be6;
	outline: 1px solid #006be6;
}

/* Date picker  */

.topMatter {
	background: #f4f6f7;
	border-bottom: 1px solid #d6dde2;
}

.monthTrigger,
.yearTrigger,
.previous,
.next {
	background: 0;
	border: 1px solid transparent;
	color: inherit;
	cursor: pointer;
	font-family: inherit;
	line-height: inherit;
	outline: none;
	-webkit-transition: border-color 0.25s ease;
	transition: border-color 0.25s ease;
}

.monthTrigger:focus,
.monthTrigger:hover,
.yearTrigger:focus,
.yearTrigger:hover,
.previous:focus,
.previous:hover,
.next:focus,
.next:hover {
	border-color: #006be6;
}

.monthTrigger,
.yearTrigger {
	color: #000000;
	font-size: 14px;
	padding: 8px;
	text-transform: uppercase;
}

.monthTriggerActive,
.yearTriggerActive {
	border-color: transparent;
	border-bottom-color: #006be6;
	color: #006be6;
}

.previous,
.next {
	background-color: #f4f6f7;
	border: 1px solid #d6dde2;
	color: #000000;
	display: block;
	font-size: 24px;
	line-height: 1;
	padding: 12px 0;
	position: absolute;
	top: 50%;
	width: 18px;
}

.previous i,
.next i {
	position: relative;
	left: -4px;
}

.previous {
	left: 0;
	-webkit-transform: translate(-100%, -50%);
	        transform: translate(-100%, -50%);
}

.next {
	right: 0;
	-webkit-transform: translate(100%, -50%);
	        transform: translate(100%, -50%);
}

.monthGrid,
.yearGrid {
	position: relative;
	background-color: #ffffff;
}

.monthFields,
.yearFields {
	border: 0;
	margin: 0;
	padding: 0;
}

.monthRadio,
.yearRadio {
	display: block;
	float: left;
	position: relative;
	text-align: center;
	text-transform: capitalize;
}

.monthRadio {
	width: 33.33%;
}

.yearRadio {
	width: 20%;
}

.monthRadioLabel,
.yearRadioLabel {
	border: 1px solid transparent;
	cursor: pointer;
	display: block;
	text-decoration: none;
	padding: 8px;
	-webkit-transition: all 0.25s ease;
	transition: all 0.25s ease;
}

.monthRadioChecked .monthRadioLabel,
.yearRadioChecked .yearRadioLabel {
	background-color: #006be6;
	color: #ffffff;
}

.monthRadioInput,
.yearRadioInput {
	height: 100%;
	left: 0;
	margin: 0;
	opacity: 0;
	position: absolute;
	top: 0;
	width: 100%;
}

.monthRadio:hover .monthRadioLabel,
.yearRadio:hover .yearRadioLabel {
	color: #006be6;
	border: 1px solid #d6dde2;
}

.monthRadioChecked:hover .monthRadioLabel,
.yearRadioChecked:hover .yearRadioLabel,
.monthRadioInput:focus ~ .monthRadioLabel,
.yearRadioInput:focus ~ .yearRadioLabel {
	background-color: #ffffff;
	color: #006be6;
	border: 1px solid #006be6;
}



