.root, .root * { -webkit-box-sizing: border-box; box-sizing: border-box; }

.root {
	display: block;
	min-height: 24px;
	padding: 0 0 0 24px;
	position: relative;
}

.input {
	height: 100%;
	left: 0;
	margin: 0;
	opacity: 0;
	position: absolute;
	top: 0;
	width: 100%;
}

.input:focus {
	outline: none;
}

.inputWrapper {
	left: 0;
	position: absolute;
	top: 4px;
}

.inputWrapper::before,
.inputWrapper::after {
	border-radius: 50%;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
	content: '';
	display: block;
	-webkit-transition: all 0.25s ease;
	transition: all 0.25s ease;
}

.inputWrapper::before {
	background-color: #ffffff;
	border: 1px solid #5c6c7c;
	height: 16px;
	width: 16px;
}

.inputWrapper::after {
	background-color: #5c6c7c;
	height: 8px;
	left: 4px;
	position: absolute;
	top: 4px;
	-webkit-transform: scale(0);
	        transform: scale(0);
	width: 8px;
}

/* focus styles */

.focused .inputWrapper:before {
	border: 2px solid #006be6;
	-webkit-box-shadow: 0 2px 2px 0 rgba(0, 107, 230, 0.2);
	        box-shadow: 0 2px 2px 0 rgba(0, 107, 230, 0.2);
}

.focused .inputWrapper:after {
	background-color: #006be6;
}

/* checked style */

.checked .inputWrapper::after {
	-webkit-transform: scale(1);
	        transform: scale(1);
}

/* disabled and readonly styles */

.disabled .inputWrapper::before,
.readonly .inputWrapper::before {
	border-color: #d6dde2;
	background-color: #f4f6f7;
}

/* required style, only applies to inputs with a label */

.required span::after {
	content: '*';
}

/* invalid style */

.invalid .inputWrapper::before {
	border-color: #eb1313;
}

.invalid .inputWrapper::after {
	background-color: #eb1313;
}

/* valid style */

.valid .inputWrapper::before {
	border-color: #188701;
}

.valid .inputWrapper::after {
	background-color: #188701;
}



