.tabButtons {
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
}

.tabButton {
	border-bottom: 1px solid #d6dde2;
	border-left: 1px solid transparent;
	border-right: 1px solid transparent;
	border-top: 1px solid transparent;
	color: #5c6c7c;
	cursor: pointer;
	display: inline-block;
	-webkit-box-flex: 1;
	    -ms-flex: 1;
	        flex: 1;
	outline: none;
	overflow: hidden;
	padding: 16px 4px;
	position: relative;
	text-align: center;
	text-overflow: ellipsis;
	vertical-align: top;
	white-space: nowrap;
	width: 128px;
	margin: 0;
}

.tabButton:hover:not(.disabledTabButton):not(.activeTabButton) {
	background-color: #f4f6f7;
	border-color: #f4f6f7;
	color: #000000;
}

.tabButton:focus:not(.disabledTabButton) {
	border-color: #006be6;
	-webkit-box-shadow: 0 2px 2px 0 rgba(0, 107, 230, 0.2);
	        box-shadow: 0 2px 2px 0 rgba(0, 107, 230, 0.2);
}

.activeTabButton {
	border-bottom-color: #006be6;
	color: #006be6;
}

.disabledTabButton {
	color: #d6dde2;
	cursor: default;
}

.close {
	background: none;
	border: none;
	cursor: pointer;
	font-size: 0;
	padding: 1px 3px;
	position: absolute;
	right: 5px;
	top: 50%;
	-webkit-transform: translateY(-50%);
	        transform: translateY(-50%);
}

.close:after {
	content: '✕';
	display: block;
	font-size: 14px;
}

.closeable {
	padding-right: 20px;
}

.tab {
	position: relative;
}

.alignLeft .tabs {
	display: inline-block;
	vertical-align: top;
	width: calc(100% - 128px);
}

.alignLeft .tabButtons {
	display: inline-block;
	vertical-align: top;
}

.alignLeft .tabButton {
	border-bottom: 1px solid transparent;
	border-left: 1px solid transparent;
	border-right: 1px solid #d6dde2;
	border-top: 1px solid transparent;
	display: block;
}

.alignLeft .activeTabButton {
	border-right-color: #006be6;
	color: #006be6;
}

.alignRight .tabs {
	display: inline-block;
	vertical-align: top;
	width: calc(100% - 128px);
}

.alignRight .tabButtons {
	display: inline-block;
	vertical-align: top;
}

.alignRight .tabButton {
	border-bottom: 1px solid transparent;
	border-left: 1px solid #d6dde2;
	border-right: 1px solid transparent;
	border-top: 1px solid transparent;
	display: block;
}

.alignRight .activeTabButton {
	border-left-color: #006be6;
	color: #006be6;
}

.alignBottom .tabButton {
	border-bottom-color: transparent;
	border-top-color: #d6dde2;
}

.alignBottom .activeTabButton {
	border-bottom-color: transparent;
	border-top-color: #006be6;
}



