}

.root {
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
	color: #000000;
	display: inline-block;
	font-size: 16px;
	line-height: 24px;
	position: relative;
	width: 100%;
}

.root * {
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
}

.root input {
	min-width: 200px;
	padding: 8px 48px 8px 8px;
	width: 100%;
}

.clearable input {
	padding-right: 64px;
}

.trigger {
	-webkit-appearance: none;
	   -moz-appearance: none;
	        appearance: none;
	background-color: transparent;
	border-radius: 0;
	border: 1px solid transparent;
	border-left: 1px solid #d6dde2;
	bottom: 1px;
	color: #5c6c7c;
	cursor: pointer;
	display: block;
	font-size: 24px;
	line-height: 1;
	outline: 0;
	padding: 8px;
	position: absolute;
	right: 0;
	top: 1px;
	-webkit-transition: border-color 0.25s ease;
	transition: border-color 0.25s ease;
}

.trigger:focus {
	border-color: #006be6;
}

.dropdown {
	background-color: #ffffff;
	border: 1px solid #d6dde2;
	-webkit-box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.2);
	        box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.2);
	opacity: 0;
	position: absolute;
	-webkit-transition: all 0.25s ease;
	transition: all 0.25s ease;
	width: 100%;
}

.open .dropdown {
	opacity: 1;
	z-index: 200;
}

.option {
	background-color: #ffffff;
	border: 1px solid transparent;
	cursor: pointer;
	line-height: 24px;
	padding: 8px;
	-webkit-transition: background 0.25s ease, border 0.25s ease;
	transition: background 0.25s ease, border 0.25s ease;
}

.selected {
	background-color: #f4f6f7;
}

.invalid .trigger {
	border-left-color: rgb(247, 197, 197);
	color: #eb1313;
}

.valid .trigger {
	border-left-color: rgb(189, 240, 178);
	color: #188701;
}

.clear {
	background: none;
	border: 1px solid transparent;
	color: #d6dde2;
	cursor: pointer;
	font-size: 16px;
	line-height: 1;
	outline: none;
	padding: 0;
	position: absolute;
	right: 48px;
	top: 50%;
	-webkit-transform: translateY(-50%);
	        transform: translateY(-50%);
}

.clear:focus {
	border-color: #006be6;
}

/* styles for backward compatibility with widgets 2.0.0 */

.trigger,
.clear {
	font-size: 0;
}

.trigger * {
	font-size: 24px;
}

.clear * {
	font-size: 16px;
}



