}

.root,
.root *,
.root *::before,
.root *::after {
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
}

.root {
	display: block;
	font-size: 16px;
	line-height: 24px;
	min-height: 24px;
	padding: 0 0 0 24px;
	position: relative;
}

.input {
	height: 100%;
	left: 0;
	margin: 0;
	opacity: 0;
	position: absolute;
	top: 0;
	width: 100%;
}

.inputWrapper {
	composes: checkIcon icon from './icon.m.css';
	left: 0;
	position: absolute;
	top: 4px;
}

.inputWrapper::before,
.inputWrapper::after {
	display: block;
	height: 16px;
	width: 16px;
}

.inputWrapper::after {
	background-color: #ffffff;
	border: 1px solid #5c6c7c;
	content: '';
}

.inputWrapper::before {
	color: #5c6c7c;
	font-size: 14px;
	left: 0;
	line-height: 16px;
	position: absolute;
	text-align: center;
	top: 0;
	-webkit-transform: scale(0);
	        transform: scale(0);
	-webkit-transition: all 0.25s ease;
	transition: all 0.25s ease;
	width: 100%;
}

/* checked style */

.checked .inputWrapper::before {
	-webkit-transform: scale(1);
	        transform: scale(1);
}

/* Toggle mode */

.toggle {
	padding-left: 0;
}

.toggle span {
 color: #000000;
}

.toggle .inputWrapper {
	width: auto;
	position: static;
	display: inline-block;
	padding-right: 8px;
}

.toggle .inputWrapper::before,
.toggle .inputWrapper::after {
	display: none;
}

.toggleSwitch {
	background-color: #d6dde2;
	border: 1px solid #5c6c7c;
	border-radius: 12px;
	display: inline-block;
	height: 24px;
	position: relative;
	-webkit-transition: background 0.25s ease, border 0.25s ease;
	transition: background 0.25s ease, border 0.25s ease;
	vertical-align: middle;
	width: 42px;
}

.toggleSwitch::before,
.toggleSwitch::after {
	border-radius: 12px;
	content: "";
	height: 24px;
	left: -1px;
	position: absolute;
	top: -1px;
	-webkit-transition: all 0.25s ease;
	transition: all 0.25s ease;
	width: 24px;
}

.toggleSwitch::before {
	background-color: #ffffff;
	border: 1px solid #5c6c7c;
}

.toggleSwitch::after {
	background-color: #006be6;
	-webkit-transform: translate(0, 0) scale(0);
	        transform: translate(0, 0) scale(0);
}

.onLabel,
.offLabel {
	color: #5c6c7c;
	display: inline-block;
}

.onLabel {
	padding-left: 8px;
}

.offLabel {
	padding-right: 8px;
}

.checked .toggleSwitch {
	background-color: #006be6;
	border-color: #006be6;
}

.checked .toggleSwitch::before {
	border-color: #006be6;
	-webkit-transform: translate(75%, 0);
	        transform: translate(75%, 0);
}

.checked .toggleSwitch::after {
	border-color: #006be6;
	-webkit-transform: translate(75%, 0) scale(0);
	        transform: translate(75%, 0) scale(0);
}

/* Focused styles */

.focused span {
	color: #006be6;
}

.focused.toggle span {
	color: #000000;
}

.focused .inputWrapper::before {
	color: #006be6;
}

.focused .inputWrapper::after {
	border: 2px solid #006be6;
	-webkit-box-shadow: 0 2px 2px 0 rgba(0, 107, 230, 0.2);
	        box-shadow: 0 2px 2px 0 rgba(0, 107, 230, 0.2);
}

.focused .toggleSwitch::before {
	border-color: #006be6;
	-webkit-box-shadow: 0 2px 2px 0 rgba(0, 107, 230, 0.2);
	        box-shadow: 0 2px 2px 0 rgba(0, 107, 230, 0.2);
}

.focused .toggleSwitch::after {
	-webkit-transform: translate(0, 0) scale(0.5);
	        transform: translate(0, 0) scale(0.5);
}

.focused.checked .toggleSwitch {
	-webkit-box-shadow: 0 2px 2px 0 rgba(0, 107, 230, 0.2);
	        box-shadow: 0 2px 2px 0 rgba(0, 107, 230, 0.2);
}

.focused.checked .toggleSwitch::after {
	-webkit-transform: translate(75%, 0) scale(0.5);
	        transform: translate(75%, 0) scale(0.5);
}

.focused .onLabel,
.focused .offLabel {
	color: #006be6;
}

/* disabled and readonly styles */

.disabled .inputWrapper::after,
.readonly .inputWrapper::after {
	background-color: #f4f6f7;
	border-color: #d6dde2;
}

.disabled .toggleSwitch,
.readonly .toggleSwitch {
	background-color: #f4f6f7;
	border-color: #5c6c7c;
}

.disabled .toggleSwitch::before,
.readonly .toggleSwitch::before {
	background-color: #f4f6f7;
	border-color: #5c6c7c;
}

/* invalid style */

.invalid .inputWrapper::after {
	border-color: #eb1313;
}

.invalid .inputWrapper::before {
	color: #eb1313;
}

/* valid style */

.valid .inputWrapper::after {
	border-color: #188701;
}

.valid .inputWrapper::before {
	color: #188701;
}



