.root, .root *, .root:before, .root:after { -webkit-box-sizing: border-box; box-sizing: border-box; }

.root {
	background-color: #ffffff;
	border: 1px solid #d6dde2;
	color: #006be6;
	cursor: pointer;
	display: inline-block;
	font-size: 16px;
	font-weight: bold;
	line-height: 24px;
	min-width: 160px;
	padding: 8px;
	-webkit-transition: all 0.25s ease;
	transition: all 0.25s ease;
	margin: 0;
}

.root:hover,
.root:focus {
	border-color: #006be6;
	-webkit-box-shadow: 0 2px 2px 0 rgba(0, 107, 230, 0.2);
	        box-shadow: 0 2px 2px 0 rgba(0, 107, 230, 0.2);
	outline: none;
}

.root:hover .addon,
.root:focus .addon {
	border-left-color: #006be6;
}

.pressed {
	background-color: #006be6;
	border-color: #006be6;
	color: #ffffff;
}

.pressed .addon {
	border-left-color: rgb(41, 141, 255);
}

.popup {
	padding-right: 48px;
	position: relative;
}

.addon {
	border-left: 1px solid #d6dde2;
	line-height: 24px;
	padding: 8px;
	position: absolute;
	right: 0;
	text-align: center;
	top: 0;
	width: 40px; /* square icon addons */
	-webkit-transition: border 0.25s ease;
	transition: border 0.25s ease;
}

.disabled,
.disabled:hover {
	background-color: #f4f6f7;
	border-color: #d6dde2;
	-webkit-box-shadow: none;
	        box-shadow: none;
	color: #5c6c7c;
	cursor: default;
}



