}

.root, .root * { -webkit-box-sizing: border-box; box-sizing: border-box; }

.root {
	font-size: 16px;
}

.inputWrapper {
	position: relative;
}

/* custom input styles */

.trigger {
	background-color: #ffffff;
	border: 1px solid #d6dde2;
	border-bottom-color: #5c6c7c;
	cursor: pointer;
	font-size: inherit;
	line-height: 24px;
	min-height: calc(40px + 2);
	outline: none;
	padding: 8px 24px 8px 8px;
	position: relative;
	text-align: left;
	width: 100%;
	-webkit-appearance: none;
	   -moz-appearance: none;
	        appearance: none;
	border-radius: 0;
}

.trigger:focus {
	border-color: #006be6;
	-webkit-box-shadow: 0 2px 2px 0 rgba(0, 107, 230, 0.2);
	        box-shadow: 0 2px 2px 0 rgba(0, 107, 230, 0.2);
}

.placeholder {
	color: #5c6c7c;
}

.arrow {
	border-left: 1px solid #d6dde2;
	bottom: 1px;
	color: #5c6c7c;
	font-size: 24px;
	line-height: 24px;
	padding: 8px;
	pointer-events: none;
	position: absolute;
	right: 0;
	top: 0;
}

.dropdown {
	-webkit-box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.2);
	        box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.2);
	opacity: 0;
	pointer-events: none;
	position: absolute;
	-webkit-transition: opacity 0.25s ease;
	transition: opacity 0.25s ease;
	width: 100%;
}

.open {
	z-index: 200;
}

.open .dropdown {
	opacity: 1;
	pointer-events: all;
}

/* native input styles */

.input {
	-webkit-appearance: none;
	   -moz-appearance: none;
	        appearance: none;
	background-color: #ffffff;
	border: 1px solid #d6dde2;
	border-bottom-color: #5c6c7c;
	border-radius: 0;
	cursor: pointer;
	display: block;
	font-size: inherit;
	line-height: 24px;
	min-width: 200px;
	outline: 0;
	padding: 8px 24px 8px 8px;
	-webkit-transition: border 0.25s ease;
	transition: border 0.25s ease;
	width: 100%;
}

.input:focus {
	border-color: #006be6;
	-webkit-box-shadow: 0 2px 2px 0 rgba(0, 107, 230, 0.2);
	        box-shadow: 0 2px 2px 0 rgba(0, 107, 230, 0.2);
}

/* IE 10+ */

.input::-ms-expand {
	display: none;
}

/* State classes */

.disabled .input,
.readonly .input,
.disabled .trigger,
.readonly .trigger {
	background-color: #f4f6f7;
}

.invalid .input,
.invalid .trigger {
	border-color: rgb(247, 197, 197);
	border-bottom-color: #eb1313;
}

.invalid .input:focus,
.invalid .trigger:focus {
	border-color: #eb1313;
	-webkit-box-shadow: 0 2px 2px 0 rgba(235, 19, 19, 0.2);
	        box-shadow: 0 2px 2px 0 rgba(235, 19, 19, 0.2);
}

.invalid .arrow {
	border-left-color: rgb(247, 197, 197);
	color: #eb1313;
}

.valid .input,
.valid .trigger {
	border-color: rgb(189, 240, 178);
	border-bottom-color: #188701;
}

.valid .input:focus,
.valid .trigger:focus {
	border-color: #188701;
	-webkit-box-shadow: 0 2px 2px 0 rgba(24, 135, 1, 0.2);
	        box-shadow: 0 2px 2px 0 rgba(24, 135, 1, 0.2);
}

.valid .arrow {
	border-left-color: rgb(189, 240, 178);
	color: #188701;
}



