.root {
	border: 1px solid #d6dde2;
	position: relative;
}

.root:focus {
	border-color: #006be6;
	-webkit-box-shadow: 0 2px 2px 0 rgba(0, 107, 230, 0.2);
	        box-shadow: 0 2px 2px 0 rgba(0, 107, 230, 0.2);
	outline: none;
}

.option {
	background-color: #ffffff;
	border: 1px solid transparent;
	cursor: pointer;
	line-height: 24px;
	padding: 8px;
	-webkit-transition: background 0.25s ease, border 0.25s ease;
	transition: background 0.25s ease, border 0.25s ease;
}

.option:hover {
	background-color: #f4f6f7;
}

.focused .activeOption,
.root:focus .activeOption {
	border: 1px solid #006be6;
}

.disabledOption {
	background-color: #f4f6f7;
	color: #5c6c7c;
	cursor: default;
}

.selectedOption {
	composes: checkIcon from './icon.m.css';
	background-color: #f4f6f7;
	padding-right: 32px;
	position: relative;
}

.selectedOption::before {
	font-family: 'dojo2BaseTheme';
	position: absolute;
	right: 8px;
	top: 50%;
	-webkit-transform: translateY(-50%);
	        transform: translateY(-50%);
}



