.titleButton {
	background-color: #ffffff;
	border: 1px solid #d6dde2;
	color: #5c6c7c;
	cursor: pointer;
	font-size: 16px;
	padding: 8px 8px 8px 32px;
	position: relative;
	width: 100%;
	margin: 0;
}

.titleButton:focus {
	border-color: #006be6;
	-webkit-box-shadow: 0 2px 2px 0 rgba(0, 107, 230, 0.2);
	        box-shadow: 0 2px 2px 0 rgba(0, 107, 230, 0.2);
	outline: none;
}

.titleButton:hover {
	background: #f4f6f7;
	color: #000000;
}

.content {
	border-bottom: 1px solid #d6dde2;
	border-left: 1px solid #d6dde2;
	border-right: 1px solid #d6dde2;
	padding: 8px;
}

.contentTransition {
	-webkit-transition: margin-top ease-in-out 0.25s;
	transition: margin-top ease-in-out 0.25s;
}

.open .titleButton {
	color: #006be6;
}

.arrow {
	position: absolute;
	left: 8px;
	top: 10px;
}

.open .arrow {
	top: 10px;
}



