.addon {
	background-color: #f4f6f7;
	border: 1px solid #d6dde2;
	border-bottom-color: #5c6c7c;
	color: #5c6c7c;
	-webkit-box-flex: 0;
	    -ms-flex: 0 0 auto;
	        flex: 0 0 auto;
	font-size: inherit;
	line-height: 24px;
	padding: 8px;
	-webkit-transition: border 0.25s ease;
	transition: border 0.25s ease;
}

.addonAfter {
	border-left: none;
}

.addonBefore {
	border-right: none;
}

.input {
	composes: input from './text-input.m.css';
	-webkit-box-flex: 1;
	    -ms-flex: 1 1 auto;
	        flex: 1 1 auto;
}

.inputWrapper {
	composes: inputWrapper from './text-input.m.css';
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
}

/* focus styles */

.focused .inputWrapper {
	-webkit-box-shadow: 0 2px 2px 0 rgba(0, 107, 230, 0.2);
	        box-shadow: 0 2px 2px 0 rgba(0, 107, 230, 0.2);
}

.input:focus {
	border-left-color: #d6dde2;
	-webkit-box-shadow: none;
	        box-shadow: none;
}

.focused .addon {
	border-color: #006be6;
}



